/*Login styling*/
@import url(https://fonts.googleapis.com/css?family=Roboto:300);

.login-page {

  padding: 8% 0 0;
  margin: auto;
}
.form {
  position: relative;
  z-index: 1;

  background: white; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(white, #E1F3FD); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(white, #E1F3FD); /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
  background: -webkit-gradient(linear, left top, left bottom, from(white), to(#E1F3FD));
  background: linear-gradient(white, #E1F3FD); /* Standard syntax */
  max-width: 660px;
  min-width: 230px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  -webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
          box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.form button {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #009FE3;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  -o-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button:hover,.form button:active,.form button:focus {
  background: #006C9A;
}
.form .message {
  margin: 15px 0 0;
  color: grey;
  font-size: 13px;
}
.form .message a {
  color: #009FE3;
  text-decoration: none;
}
.form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #D9EDF7;
  width: 100%;
  border: 0;
  margin: 15px 0 40px;
  padding: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 14px;
}

